import React, { useState } from 'react';
import axios from 'axios';

const AddCommentForm = ({ articleName, setArticleInfo }) => {

    const [username, setUserName] = useState("");
    const [commentText, setCommentText] = useState("");

    const addComment = async () => {
        axios({
            method: 'post',
            url: `/api/article/${articleName}/add-comment`,
            data: { username, text: commentText },
            headers: { 'Content-Type': 'application/json' }
        }).then(respose => {
            setArticleInfo(respose.data);
            setUserName("");
            setCommentText("");
        });
    }

    return (
        <div id="comments-form">
            <div className="form-group">
                <label>Name:</label>
                <input className="form-control" type="text" value={username} onChange={(e) => setUserName(e.target.value)} />
            </div>
            <div className="form-group">
                <label>Name</label>
                <textarea className="form-control" rows="4" cols="40" value={commentText} onChange={(e) => setCommentText(e.target.value)} />
            </div>
            <button className="btn btn-success" onClick={() => { addComment() }}>Add Comment</button>
        </div>
    )
};

export default AddCommentForm;