import React, { useState, useEffect } from 'react';
import articleContent from "./article-cointent";
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';
import articles from './article-cointent';
import axios from "axios";

const ArticlePage = ({ match }) => {

    const name = match.params.name;
    const article = articleContent.find(article => article.name === name)

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await axios(`/api/article/${name}`);
            setArticleInfo(result.data);
        }
        fetchData();
    }, [name]);

    if (!article) return <h1>Article does not exist!</h1>;

    const otherArticles = articleContent.filter(article => articles.name !== name);

    return (
        <div className="text-left font-xs">
            <h1 className="text-left">{article.title}</h1>
            <h3 className="text-left">{article.name}</h3>
            <UpvoteSection articleName={name} upvotes={article.upvotes} SetArticleInfo={setArticleInfo} />
            <p className="text-justify">{article.content}</p>
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h3 className="text-left">Other Articles</h3>
            <ArticlesList articles={otherArticles} />
        </div>

    );

};

export default ArticlePage;