import React from 'react';
import axios from 'axios';

const UpvoteSection = ({articleName, upvotes, SetArticleInfo}) => {

    const upVotedArticle = async () => {
        axios({
            method: 'post',
            url:`/api/article/${articleName}/upvote`
        }).then(result => {SetArticleInfo(result.data)});
    };


    return (
        <div id="upvote-section">
            <button className="btn btn-primary" onClick={()=>upVotedArticle()}>Add Upvote</button>
            <p>This post has been upvoted {upvotes} times</p>
        </div>

    )
};

export default UpvoteSection;